<template>
	<div style="height: 100%;">
		<el-row :gutter="20" style="height: 100%;">
		  	<el-col :span="5" style="height: 100%;">
		  		<el-card class="box-card" style="height: 100%;">
				  	<div slot="header" class="clearfix">
				    	<span></span>
				    	<el-button style="float: right; padding: 3px 0" type="text" @click="init()"></el-button>
				  	</div>
				  	<el-tree :data="types" v-loading="loadingtree" :default-expand-all="true" @node-click="handleNodeClick"></el-tree>
				</el-card>
		  	</el-col>
		  	<el-col :span="19" style="height: 100%;">
		  		<el-card class="box-card" style="height: 100%;overflow-y: auto;">
				  	<div slot="header" class="clearfix">
				    	<span></span>
				    	<el-button style="float: right; padding: 3px 0" type="text" @click="handleAddUI()" icon="el-icon-plus"></el-button>
				    	<el-button style="float: right; padding: 3px 0" type="text" @click="searchs()" icon="el-icon-refresh"></el-button>
				  	</div>
				  	<el-table v-loading="loadingtable" :data="details" border stripe size="small">
						<el-table-column prop="icon" label="" width="150">
							<template slot-scope="scope">
								<img :src="scope.row.icon" />
							</template>
						</el-table-column>
						<el-table-column prop="iconbig" label="" width="150">
							<template slot-scope="scope">
								<el-link type="primary" @click="showImgUI(scope.row.iconbig)"></el-link>
							</template>
						</el-table-column>
						<el-table-column prop="name" label="" width="150"></el-table-column>
						<el-table-column prop="suffix" label="" width="100"></el-table-column>
						<el-table-column prop="componentname" label=""></el-table-column>
						<el-table-column prop="cz" label="" width="150">
							<template slot-scope="scope">
					        	<el-button type="text" size="mini" icon="el-icon-edit" @click="handleEditUI(scope.row.id)"></el-button>
					        	<el-button type="text" size="mini" icon="el-icon-delete" @click="handleDelete(scope.row.id)"></el-button>
							</template>
						</el-table-column>
					</el-table>
					<!---->
					<el-pagination
						@size-change="sizechange"
						@current-change="currentchange"
						@prev-click="prevClick"
			      		@next-click="nextClick"
			      		:page-sizes="[10]"
			      		:current-page="currentpage"
			      		:page-size="pagesize"
			      		:total="total"
			      		layout="total, sizes, prev, pager, next, jumper">
			    	</el-pagination>
				</el-card>
		  	</el-col>
		</el-row>
		
		<!---->
		<el-dialog :title="dialog.title" :visible.sync="dialog.visible" :width="dialog.width" top="60px"
			:show-close="false" :close-on-click-modal="false" :close-on-press-escape="false">
			<add ref="form" v-if="dialog.type=='add'" v-on:handleResult="handleResult"></add>
			<edit ref="form" v-if="dialog.type=='edit'" v-on:handleResult="handleResult"></edit>
			<showimg ref="form" v-if="dialog.type=='showimg'"></showimg>
			
			<div slot="footer" class="dialog-footer">
    			<el-button @click="handleClose()"> </el-button>
    			<el-button type="primary" @click="handleConfirm()" v-if="dialog.type!='showimg'"> </el-button>
  			</div>
		</el-dialog>
	</div>
</template>

<script>
	import add from './add.vue';
	import edit from './edit.vue';
	import showimg from './dialog_showimg.vue';
	export default {
		data(){
			return {
				loadingtree:false,
				loadingtable:false,
				
				types:[],
				details:[],
				typecode:'all',
				currentpage:1,
      			pagesize:10,
      			total:0,
      			
				dialog:{
      				width:"600px",
      				type:"",//addedit
      				visible: false,
      				title:''
      			}
			}
		},
		components:{
			add,
			edit,
			showimg
		},
		created(){
			this.findTypeTree();
			//this.searchs();
		},
		methods:{
			findTypeTree(){
				this.loadingtree=true;
				this.$http.post('type/findTypeTree',{
					"token":sessionStorage.getItem("token")
				}).then(response => {
					var data=response.body;
					if(data.code==0){
						this.types=data.data;
					}else{
						this.alertMsg(data.code,data.msg);
					}
					this.loadingtree=false;
				});
			},
			init(){
				this.$confirm(', ?', '', {
          			confirmButtonText: '',
          			cancelButtonText: '',
          			type: 'warning'
        		}).then(() => {
        			this.loadingtree=true;
        			this.loadingtable=true;
        			this.$http.post('type/init',{
        				"token":sessionStorage.getItem("token")
        			}).then(response => {
						var data=response.body;
						if(data.code==0){
							this.findTypeTree();
							this.searchs();
						}
						this.alertMsg(data.code,data.msg);
						this.loadingtree=false;
        				this.loadingtable=false;
					});
        		});
			},
			handleNodeClick(data) {
				this.typecode=data.code;
				if(data.code!="all"){
					this.searchs();
				}
			},
			////////////////////////////////////////////////////////////////////////
			searchs(){
				this.currentpage=1;
				this.findTypeDetail();
			},
			findTypeDetail(){
				this.loadingtable=true;
				this.$http.post('typesuffix/findList',{
					"typecode":this.typecode,
					"page":this.currentpage,
					"limit":this.pagesize,
					"token":sessionStorage.getItem("token")
				}).then(response => {
					var data=response.body;
					if(data.code==0){
						var rows=data.rows;
						if(rows!=null&&rows.length!=0){
							for(var i=0;i<rows.length;i++){
								var d=rows[i];
								d.icon="data:image/png;base64,"+d.icon;
								d.iconbig="data:image/png;base64,"+d.iconbig;
							}
						}
						
						this.details=rows;
						this.total=data.totalElements;
					}else{
						this.alertMsg(data.code,data.msg);
					}
					this.loadingtable=false;
				});
			},
			sizechange(pagesize){
				this.pagesize=pagesize;
				this.findTypeDetail();
			},
			currentchange(currentpage){
				this.currentpage=currentpage;
				this.findTypeDetail();
			},
			prevClick(){
				this.currentpage=this.currentpage-1;
				this.findTypeDetail();
			},
			nextClick(){
				this.currentpage=this.currentpage+1;
				this.findTypeDetail();
			},
			////////////////////////////////////////////////////////////////////////
			handleAddUI(){
				this.dialog.width="600px";
				this.dialog.type="add";
				this.dialog.title="";
				this.dialog.visible=true;
			},
			handleEditUI(id){
				this.dialog.width="600px";
				this.dialog.type="edit";
				this.dialog.title="";
				this.dialog.visible=true;
				setTimeout(()=>{
					this.$refs.form.showInfo(id);
				},0);
			},
			showImgUI(icon){
				this.dialog.width="400px";
				this.dialog.type="showimg";
				this.dialog.title="";
				this.dialog.visible=true;
				setTimeout(()=>{
					this.$refs.form.showInfo(icon);
				},0);
			},
			////////////////////////////////////////////////////////////////////////
			handleClose(){
				this.dialog.visible=false;
			},
			handleConfirm(){
				this.$refs.form.save();
			},
			handleResult(code){
				this.handleClose();
				this.searchs();
			},
			////////////////////////////////////////////////////////////////////////
			handleDelete(id){
				this.$confirm(', ?', '', {
          			confirmButtonText: '',
          			cancelButtonText: '',
          			type: 'warning'
        		}).then(() => {
        			this.loadingtable=true;
        			this.$http.post('typesuffix/delete',{
        				"id":id,
        				"token":sessionStorage.getItem("token")
        			}).then(response => {
						var data=response.body;
						if(data.code==0){
							this.searchs();
						}
						this.alertMsg(data.code,data.msg);
        				this.loadingtable=false;
					});
        		});
			}
		}
	}
</script>

<style scoped="scoped">
</style>


